








import Vue from 'vue'

export default Vue.extend({
  name: 'SLink',
  methods: {
    handleClick(e: MouseEvent) {
      e.preventDefault()
      this.$router.push(this.to)
    },
  },
  props: {
    to: {
      type: String,
      required: true,
    },
    linkStyle: {
      type: Object,
      default() {
        return {}
      },
    },
    hover: {
      type: Boolean,
      default() { return true },
    },
    hoverBlue: {
      type: Boolean,
      default() { return false },
    },
  },
})
